.startButton {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-image: url(./img/microphone.png);
	background-color: red;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 100px;
	width: 100px;
	border: none;
	border-radius: 50%;
	background-size: cover;
}

.startButton span {
	display: none;
}

.stopButton {
	position: absolute;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: red;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	height: 50px;
	width: 50px;
	border: none;
}

.stopButton span {
	display: none;
}

.recordingTime {
	font-size: xx-large;
	text-align: center;
	position: absolute;
	top: 40%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.recordingText {
	color: red;
    -webkit-animation: fadingLight 1.5s ease-in infinite; /* Safari 4+ */
    -moz-animation:    fadingLight 1.5s ease-in infinite; /* Fx 5+ */
    -o-animation:      fadingLight 1.5s ease-in infinite; /* Opera 12+ */
    animation:         fadingLight 1.5s ease-in infinite; /* IE 10+, Fx 29+ */
}

.stopwatch {
	font-size: x-large;
}

.audioControls {
	position: absolute;
	top: 80%;
	left: 50%;
	transform: translate(-50%, -50%);
}

@-webkit-keyframes fadingLight {
	0%, 100% {
		opacity: 100%;
	}
	50% {
		opacity: 10%;
	}
}
@-moz-keyframes fadingLight {
	0%, 100% {
		opacity: 100%;
	}
	50% {
		opacity: 10%;
	}
}
@-o-keyframes fadingLight {
	0%, 100% {
		opacity: 100%;
	}
	50% {
		opacity: 10%;
	}
}
@keyframes fadingLight {
	0%, 100% {
		opacity: 100%;
	}
	50% {
		opacity: 10%;
	}
}
